import React, { Component } from 'react';
import { Card, Row, Col, Button, message, Input,Checkbox, Tag } from 'antd';
import { getStorage } from '../../../../config/mUtils'
import { getMarketRule,saveMarketRule } from '../../../../config/getData';
import history from '../../../public/history';

import './UseCouponStyle.scss';

export default class UseCouponStyle extends Component{
    constructor(props){
        super(props);
        this.state = {
            checkboxGroupOptions:[], //使用规则多选框
            checkArr:[],         //使用规则选中值
            customRulelist:[], //自定义规则选中值
        }
    }   

    componentDidMount(){
        this.initPageData();
    }

    //初始化数据
    initPageData = () => {
        getMarketRule({shopId:getStorage("storeMsgStorage").toJson().shopId}).then((res)=>{
            console.log(res)
            var resData = res.data;
            console.log(resData.data);
            if(resData.errorCode === "0000"){
                if(resData.data.commonRulelist.length){
                    var {commonRulelist,customRulelist} = resData.data;
                    // 设置state里面的使用规则多选框和使用规则选中值
                    var allCheckArr = [];
                    commonRulelist = commonRulelist.map((item,index)=>{
                        console.log(Number(item.isChecked))
                        item.label = item.ruleText;
                        item.value = item.ruleId;
                        if(Number(item.isChecked)){
                            allCheckArr.push(item.ruleId);
                        }
                        return item;
                    })
                    
                    // 设置自定义规则选中值
                    customRulelist = customRulelist.map((item,index)=>{
                        return item.ruleText;
                    })
                    // console.log(allCheckArr)
                    this.setState({
                        checkboxGroupOptions:commonRulelist,
                        checkArr:allCheckArr,
                        customRulelist:customRulelist
                    })
                }   
            }
        })

    }

    backSellManagement = ()=>{
        history.push('/storeItem/sellManagement');
    }

    /**
     * 营业时间值改变并且赋值
     */
    customRulelistChange = (index,e) => {
        this.state.customRulelist[index] = e.target.value;
        this.setState({
            initData:this.state.customRulelist
        })
    }

    /**
    * 选择使用规则
    */
    userStyleSelect = (e) => {

        var checkboxGoCont = "";
            checkboxGoCont = this.state.checkboxGroupOptions.map((itemCheckbox,index)=>{
                itemCheckbox.isChecked = "0";
                e.forEach((item,index)=>{
                    if(item == itemCheckbox.ruleId){
                        itemCheckbox.isChecked = "1";
                    }
                })
                return itemCheckbox;
            })
            // console.log(checkboxGoCont)
            this.state.checkboxGroupOptions = checkboxGoCont;
            this.setState({
                checkArr:e,
            })
    }

    /**
     * 新增营业时间
     */
    addNewStyle= ()=>{
        this.state.customRulelist.push("");
        this.setState({
            initData:this.state.customRulelist
        })
    }

    /**
     * 删除规则描述
     */
    deteleCustomRulelist = (index,e)=>{
        this.state.customRulelist.splice(Number(index),1);
        this.setState({
            initData:this.state.customRulelist
        })
    }

    /**
     * 规则描述
     */
    customRulelistDom = () => {
        let customRulelist = this.state.customRulelist.map((item,index)=>{
            return (
                    <Col key={index}  xs={24} sm={24} md={24} lg={21} className="flexCenter">
                        <Input 
                            onChange={(e)=>{this.customRulelistChange(index,e)}}
                            value={item}
                            placeholder="其他优惠券规则" 
                            className="fcInput"></Input>
                        <Tag color={"#f50"} className="andTo" onClick={(e)=>{this.deteleCustomRulelist(index,e)}} >删除</Tag>
                    </Col>
            )
        })
        return customRulelist;
    }
  

    /**
     * 保存
     */
    saveCustomRule = (e)=>{
        var {checkboxGroupOptions,customRulelist} = this.state;
        var cbgoList = [];
        checkboxGroupOptions.map((item,index)=>{
            if(Number(item.isChecked)){
                cbgoList.push({ruleId:item.ruleId});
            }
        })
        var crList =  customRulelist.map((item,index)=>{
            return {ruleText:item};
        })
        var reqData = {
            commonRulelist:cbgoList,
            customRulelist:crList,
            shopId: getStorage("storeMsgStorage").toJson().shopId
        }
        console.log(reqData)
        //保存使用规则
        saveMarketRule(reqData).then((res)=>{
            // console.log(res)
            var resData = res.data;
            if(resData.errorCode === "0000"){
                message.success("保存使用规则成功!");
                this.backSellManagement();
            }else{
                message.error(resData.errorMsg);
            }
        })
    }

    render(){
        var {checkboxGroupOptions,checkArr} = this.state;
    
        return (
            <div className='useCouponStyleCont'>
                <div>
                    <Card
                        title={<span><span style={{color:'#ADADAD'}}>营销管理 / </span><span>使用规则</span></span>}
                        extra={<div>
                                  <Button onClick={()=>(this.backSellManagement())}>取消</Button>
                                  <Button type="primary" style={{marginLeft:"10px"}} onClick={()=>{this.saveCustomRule()}}>保存</Button>
                               </div>}>
                        <Row className="userStyleCont">
                            <Col  xs={24} sm={24} md={24} lg={3} className="styleDesc">
                                      使用规则:
                            </Col>
                            <Col  xs={24} sm={24} md={24} lg={10} className="styleDescCheckbox">
                            <Checkbox.Group 
                                        style={{ width: '100%' }}  
                                        className="usmcCheckBoxCont"
                                        value={checkArr}
                                        onChange={(e)=>{this.userStyleSelect(e)}} 
                                        options={checkboxGroupOptions}>
                            </Checkbox.Group>
                            </Col>
                        </Row>
                    </Card>
                    <Card
                        title={<span>
                            <span>添加其他使用规则</span>
                            <span style={{color:'#ADADAD',fontSize:'12px'}}>（如选项中没有符合要求的规则，可以自定义添加）</span>
                            </span>}
                        >
                            <Row gutter={20} className="otherUserStyle">
                                <Col  xs={24} sm={24} md={24} lg={3} className="styleDesc" style={{padding:'0'}}>
                                        规则描述:
                                </Col>

                                <Col  xs={24} sm={24} md={24} lg={21} >
                                    <Row gutter={20} >
                                        {this.customRulelistDom()}
                                    </Row>
                                </Col>

                                <Col  xs={24} sm={24} md={24} lg={3} className="styleDesc"></Col>
                                <Col  xs={24} sm={24} md={24} lg={21}>
                                    <Button type="dashed" onClick={()=> {this.addNewStyle()}} style={{color:"#d9d9d9",width:"400px"}}>+ 添加其他使用规则</Button>
                                </Col>
                            </Row>
                    </Card>
                </div>
            </div>
        )
    }


}